@charset "UTF-8";
body {
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
  color: #1A1A1A;
}

.u-bold {
  font-weight: 700;
}

.u-regular {
  font-weight: 400;
}

.u-blue {
  color: #24669a;
}

.u-pink {
  color: #f46289;
}

.u-red {
  color: #ed1c24;
}

.u-bg-yellow {
  background-color: #fff0d6;
}

.u-spacer {
  margin-bottom: 1em;
  display: block;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeLegibility;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}

.inner {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (min-width: 768px) {
  .inner {
    max-width: 1150px;
    padding-right: 25px;
    padding-left: 25px;
  }
}

.intro {
  text-align: center;
  line-height: 1.75;
}

.intro__text-fz19 {
  font-size: 1.1875rem;
}

.intro__text:last-of-type {
  margin-bottom: 3.125rem;
}

.intro__arrow {
  width: 11.6875rem;
  display: inline-block;
}

.voice {
  text-align: center;
  line-height: 1.75;
}

.voice__inner {
  border-top: 3px solid #d4145a;
  border-bottom: 3px solid #d4145a;
  position: relative;
  padding-top: 1.5625rem;
  padding-bottom: 3.125rem;
}

.voice__inner::before,
.voice__inner::after {
  content: "";
  width: 100%;
  height: 1px;
  position: absolute;
  left: 0;
  background: linear-gradient(to right, #d4145a 0%, #d4145a 50%, #ffffff 51%, #ffffff 100%) repeat-x left bottom/6px 1px;
}

.voice__inner::before {
  top: 0.5rem;
}

.voice__inner::after {
  bottom: 0.5rem;
}

.voice__head-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1.375rem;
  margin-bottom: 2.125rem;
}

.voice__head {
  flex-shrink: 0;
  font-size: 1.1875rem;
}